html, body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
  font-size: 14px;
}

#external-events {
  position: fixed;
  z-index: 2;
  top: 20px;
  left: 20px;
  width: 150px;
  padding: 0 10px;
  border: 1px solid #ccc;
  background: #eee;
}

#external-events .fc-event {
  margin: 1em 0;
  cursor: move;
}

#calendar-container {
  position: relative;
  z-index: 1;
  margin-left: 200px;
}

#calendar {
  max-width: 900px;
  margin: 20px auto;
}

.main-group {
  width: 201px;
  height: 275px;
  background: #fff 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 18px #00000029;
  border: 1px solid #43425D;
  border-radius: 10px;
  opacity: 1;
}

.main-group {
  position: absolute;
  z-index: 1;
  top: 190px;
  left: 190px;
}
