@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
.body {
  margin: 0;
  padding: 0;
}
.header {
  margin: 0 auto;
  padding: 0 auto;
  min-width: 1750px;
  align-items: center;
  height: 70px;
  background: #fff;
  grid-template-columns: 34% 5% 4% 3% 0% 5% 3% 5%;
  grid-template-rows: 1fr;
  display: grid;
}

.header__search {
  width: 16px;
  height: 16px;
  cursor: pointer;
}

.n__header__input {
  border: none;
  width: 300px;
  padding: 0px 11px;
  cursor: pointer;
}

.header__search__input {
  padding: 0px 11px;
  cursor: pointer;
}

.header__avatar {
  width: 38px;
  height: 38px;
  cursor: pointer;
}

.header__planet {
  width: 16px;
  height: 16px;
  opacity: 1;
  cursor: pointer;
}

.header__name {
  display: inline-grid;
  margin: 0;
  cursor: pointer;
}

@media (min-width: 768px) {
  .header {
    max-width: 720px;
  }
}

.fc-toolbar h2 {
  display: none;
}

.fc-toolbar {
  padding: 0;
}

.fc-button-primary:disabled {
  color: #3b86ff;
  background-color: #fff;
  border: 1px solid #d7dae2;
  height: 32px;
  box-shadow: #000 2px Blur 6px;
  width: 57px;
}

.fc-button-group button {
  height: 32px;
  border: 1px solid #d7dae2;
  background-color: #fff;
  color: #3b86ff;
}

.fc-button-group > .fc-button:not(:last-child) {
  padding: 0;
}

.fc-icon-chevron-left:before {
  content: "Back";
  font-family: "Source Sans Pro", sans-serif;
  font-size: 17px;
}

.fc-button:disabled {
  opacity: 1;
}

.fc-icon-chevron-right:before {
  content: "Next";
  font-family: "Source Sans Pro", sans-serif;
  font-size: 17px;
}

.fc-button .fc-icon {
  vertical-align: middle;
  font-size: 16px;
  width: 57px;
}

.fc-button-group > .fc-button:not(:first-child) {
  padding: 0;
  height: 32px;
  width: 57.86;
}

.fc-toolbar > * > :not(:first-child) {
  margin: 0;
}

.fc-right {
  height: 32px;
  width: 193px;
}
